// ‰∏Ä‰∏™ÁªÑ‰ª∂ÂØπÂ∫î‰∏Ä‰∏™watcher
let id = 0;
import { popTarget, pushTarget } from './dep';
import { queueWatcher } from './scheduler';

class Watcher {
    constructor(vm, exprOrFn, cb, options) {
        this.vm = vm
        this.exprOrFn = exprOrFn
        this.user = !!options.user // Ê†áËØÜÊòØ‰∏çÊòØÁî®Êà∑ÂÜôÁöÑwatcher
        this.lazy = !!options.lazy
        // ÈªòËÆ§ÁöÑdirtyÊòØËÑèÁöÑ
        // Â¶ÇÊûúÊòØËÆ°ÁÆóÂ±ûÊÄßÔºåÂàôÈªòËÆ§dirtyÊòØtrue | lazy‰πüÊòØtrue
        this.dirty = options.lazy
        this.cb = cb
        this.options = options
        this.id = id++ // ÁªôwatcherÊ∑ªÂä†Ê†áËØÜ

        // ÈªòËÆ§Â∫îËØ•ÊâßË°åexprOrFn
        // exprOrFn ÂÅö‰∫ÜÊ∏≤ÊüìÂíåÊõ¥Êñ∞
        // ÊñπÊ≥ïË¢´Ë∞ÉÁî®ÁöÑÊó∂ÂÄôÔºå‰ºöÂèñÂÄº
        if (typeof exprOrFn == 'string') {
            // ËøôÈáåÈúÄË¶ÅÂ∞ÜË°®ËææÂºèËΩ¨Êç¢ÊàêÂáΩÊï∞
            this.getter = function () {
                // ÂΩìÊï∞ÊçÆÂèñÂÄºÁöÑÊó∂ÂÄôÔºå‰ºöËøõË°å‰æùËµñÊî∂ÈõÜ
                // ÊØèÊ¨°ÂèñÂÄºÁöÑÊó∂ÂÄôÔºåÁî®Êà∑Ëá™Â∑±ÂÜôÁöÑwatcherÂ∞±‰ºöË¢´Êî∂ÈõÜ
                // ËøôÈáåÁöÑÂèñÂÄºÂèØ‰ª•Á±ªÊØîÈ°µÈù¢Ê∏≤ÊüìÁöÑÂèñÂÄº{{}}
                let path = exprOrFn.split('.')
                let obj = vm
                for (let i = 0; i < path.length; i++) {
                    obj = obj[path[i]]
                }
                return obj // Ëµ∞getterÊñπÊ≥ï
            }
        } else {
            this.getter = exprOrFn
        }
        this.deps = []
        this.depsId = new Set()
        // ÈªòËÆ§ÂàùÂßãÂåñÊâßË°åget
        // Á¨¨‰∏ÄÊ¨°Ê∏≤ÊüìÁöÑÊó∂ÂÄôÁöÑvalue
        // Â¶ÇÊûúÊòØlazyÂ∞±‰ªÄ‰πàÈÉΩ‰∏çÂÅö
        this.value = this.lazy ? undefined : this.get()
    }
    get() {
        pushTarget(this) // DepÁöÑtargetÂ∞±ÊòØ‰∏Ä‰∏™watcher

        /* ÂàõÂª∫ÂÖ≥ËÅî alert 
            * ÊØè‰∏™Â±ûÊÄßÈÉΩÂèØ‰ª•Êî∂ÈõÜËá™Â∑±ÁöÑwatcher
           * Â∏åÊúõ‰∏Ä‰∏™Â±ûÊÄßÂèØ‰ª•ÂØπÂ∫îÂ§ö‰∏™watcher
           * ‰∏Ä‰∏™watcherÂèØ‰ª•ÂØπÂ∫îÂ§ö‰∏™Â±ûÊÄß
        */
        // Á®çÂêéÁî®Êà∑Êõ¥Êñ∞ÁöÑÊó∂ÂÄôÂèØ‰ª•ÈáçÊñ∞Ë∞ÉÁî®getÊñπÊ≥ï
        // ËøôÈáåÊãøÂà∞ÁöÑÂÄºÊòØËæõÁöÑÂÄº
        const value = this.getter.call(this.vm)
        popTarget() // ËøôÈáåÂéªÈô§Dep.target,ÊòØÈò≤Ê≠¢Áî®Êà∑Âú®js‰∏≠ÂèñÂÄº‰∫ßÁîü‰æùËµñÊî∂ÈõÜ
        return value
    }
    update() {
        // ÊØèÊ¨°Êõ¥Êñ∞Êó∂ÔºåÊääwatcherÁºìÂ≠ò‰∏ãÊù• 
        // Â¶ÇÊûúÂ§öÊ¨°Ë∑üÊñ∞ÁöÑÊòØ‰∏Ä‰∏™watcherÔºåÂêàÂπ∂Êàê‰∏Ä‰∏™
        // vue‰∏≠ÁöÑÊõ¥Êñ∞ÊòØÂºÇÊ≠•ÁöÑ
        // this.get()

        // Â¶ÇÊûúÂΩìÂâçÁöÑwatcherÊòØlazyÁöÑÔºåÂàôËØ¥ÊòéÊòØËÆ°ÁÆóÂ±ûÊÄßÁöÑwatcher
        if (this.lazy) {
            this.dirty = true
        } else {
            queueWatcher(this)
        }
    }

    run() { // ÂêéÁª≠Ë¶ÅÊúâÂÖ∂‰ªñÁöÑÂäüËÉΩ
        // console.log('run')
        let newValue = this.get()
        let oldValue = this.value
        this.value = newValue // ‰∏∫‰∫Ü‰øùËØÅ‰∏ã‰∏ÄÊ¨°Êõ¥Êñ∞ÁöÑÊó∂ÂÄôÔºåËøô‰∏Ä‰∏™Êñ∞ÂÄºÊòØ‰∏ã‰∏Ä‰∏™ÁöÑËÄÅÂÄº
        if (this.user) {
            this.cb.call(this.vm, newValue, oldValue)
        }
    }
    addDep(dep) {
        let id = dep.id
        if (!this.depsId.has(id)) {
            this.depsId.add(id)
            this.deps.push(dep)
            dep.addSub(this)
        }
    }

    evaluate() {
        // ÊäädirtyÁΩÆ‰ΩçfalseÔºåËØ¥ÊòéÂèñËøáÂÄº‰∫Ü
        this.dirty = false
        // getÂ∞±ÊòØwatcher‰º†ËøõÊù•ÁöÑexprOrFn
        this.value = this.get()
    }

    depend() {
        let i = this.deps.length
        console.log('i', i)
        while (i--) {
            this.deps[i].depend()
        }
    }

}

// Âú®vue‰∏≠ÔºåÈ°µÈù¢Ê∏≤ÊüìÁöÑÊó∂ÂÄô‰ΩøÁî®ÁöÑÂ±ûÊÄßÔºåÈúÄË¶ÅËøõË°å‰æùËµñÊî∂ÈõÜ
// Êî∂ÈõÜÂØπË±°Ê∏≤ÊüìÁöÑwatcher
// Ê∏≤ÊüìÈ°µÈù¢ÂâçÔºå‰ºöÂ∞ÜÂΩìÂâçÁöÑwatcherÊîæÂà∞Dep‰∏ä
// Êàë‰ª¨Â∞ÜÊõ¥Êñ∞ÁöÑÂäüËÉΩÂ∞ÅË£ÖÊàê‰∏Ä‰∏™watcher
// ÂèñÂÄºÁöÑÊó∂ÂÄôÔºåÁªôÊØè‰∏™Â±ûÊÄßÈÉΩÂä†‰∫Ü‰∏Ä‰∏™depÂ±ûÊÄßÔºåÁî®‰∫éÂ≠òÂÇ®Ëøô‰∏™Ê∏≤Êüìwatcher => Âêå‰∏Ä‰∏™watcherÂØπÂ∫îÂ§ö‰∏™dep
// ÊØè‰∏™Â±ûÊÄßÔºåÂèØËÉΩÂØπÂ∫îÂ§ö‰∏™ËßÜÂõæÔºåÂ§ö‰∏™ËßÜÂõæÊúâÂ§ö‰∏™watcher => ‰∏Ä‰∏™Â±ûÊÄßË¶ÅÂØπÂ∫îÂ§ö‰∏™watcher
// ÂèåÂêëÂ≠òÂÇ® 
export default Watcher