import { isFunction } from './util';
import { observe } from './observe/index';
import Watcher from './observe/watcher';
import Dep from './observe/dep';

/**
 * 
 * @param {*} Vue 
 */
export function stateMixin(Vue) {
    Vue.prototype.$watch = function (key, handler, options = {}) {
        // Áî®Êà∑Ëá™Â∑±ÂÜôÁöÑwatcherÂíåÊ∏≤ÊüìwatcherÂå∫ÂàÜ
        options.user = true
        new Watcher(this, key, handler, options)
    }
}

/**
 * 
 * @param {VueÁöÑÂÆû‰æã} vm 
 * initState ËØ¥ÊòéÔºö
 * ÂØπVueÁöÑÊï∞ÊçÆËøõË°åÂàùÂßãÂåñ
 * VueÁöÑÊï∞ÊçÆÊù•Ê∫êÊúâÔºödataÔºåcomputedÔºåwatchÔºåpropsÔºåinject...
 */
export function initState(vm) {
    const opts = vm.$options
    if (opts.data) {
        // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
        initData(vm)
    }
    if (opts.computed) {
        // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
        initComputed(vm, opts.computed)
    }
    if (opts.watch) {
        // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
        initWatch(vm, opts.watch)
    }
}

function initData(vm) {
    // Áî®Êà∑ËÆæÁΩÆ$ÂºÄÂ§¥ÁöÑÂ±ûÊÄßÔºåVueÂÜÖÈÉ®‰∏ç‰ºöËøõË°å‰ª£ÁêÜ
    let data = vm.$options.data
    // vue2‰∏≠Ôºå‰ºöÂ∞Üdata‰∏≠ÁöÑÊâÄÊúâÊï∞ÊçÆÔºåËøõË°åÊï∞ÊçÆÂä´ÊåÅÔºàObject.definePropertyÔºâ
    // Â¶ÇÊûúdataÊòØÂáΩÊï∞ÔºåÂèñËøîÂõûÂÄºÔºåÂê¶ÂàôÂéªdataÊú¨Ë∫´
    // Â¶ÇÊûúÊòØÊñπÊ≥ïÁöÑËØùÔºåÊàëËøôÈáåÂ∏åÊúõÊñπÊ≥ïÈáåÈù¢ÁöÑthisÈÉΩÊåáÂêëVM
    // ÈÄöËøá_dataÂØπdata Âíå vmËøõË°åÂÖ≥ËÅî
    data = vm._data = isFunction(data) ? data.call(vm) : data
    for (let key in data) {
        proxy(vm, '_data', key)
    }
    // ÂØπÊï∞ÊçÆËøõË°åËßÇÊµã
    observe(data)
}

function proxy(vm, source, key) {
    Object.defineProperty(vm, key, {
        get() {
            return vm[source][key]
        },
        set(newValue) {
            vm[source][key] = newValue
        }
    })
}

function initWatch(vm, watch) {
    for (let key in watch) {
        let handler = watch[key]
        if (Array.isArray(handler)) {
            for (let i = 0; i < handler.length; i++) {
                // handler[i]
                createWatcher(vm, key, handler[i])
            }
        } else {
            // handler
            createWatcher(vm, key, handler)
        }
    }
}

function createWatcher(vm, key, handler) {
    // new Watcher()
    return vm.$watch(key, handler)
}

function initComputed(vm, computed) {
    const watchers = vm._computedWatchers = {}
    for (let key in computed) {
        /**
         * userDef ÂèØËÉΩÊòØÂáΩÊï∞
         * ÂèØËÉΩÊòØÂØπË±°
         * ‰æùËµñÁöÑÂ±ûÊÄßÂèòÂåñÔºåÂ∞±ÈáçÊñ∞ÂèñÂÄº
         **/
        const userDef = computed[key]
        let getter = typeof userDef === 'function' ? userDef : userDef.get
        console.log('getter', getter)
        // ËøôÈáåÊúâÂ§öÂ∞ë‰∏™getterÂ∞±Ë¶ÅÊúâÂ§öÂ∞ë‰∏™watcherÔºåÊØè‰∏Ä‰∏™ËÆ°ÁÆóÂ±ûÊÄßÁöÑÊú¨Ë¥®Â∞±ÊòØ‰∏Ä‰∏™watcher

        // Â∞ÜwathcerÂíåÂ±ûÊÄßÂÅö‰∏Ä‰∏™Êò†Â∞Ñ
        watchers[key] = new Watcher(vm, getter, () => { }, { lazy: true }) // ÈªòËÆ§‰∏çÊâßË°å
        // Â∞ÜkeyÂÆö‰πâÂú®vm‰∏ä
        defineComputed(vm, key, userDef)
    }
}

function defineComputed(vm, key, userDef) {
    let sharedProperty = {}
    if (typeof userDef == 'function') {
        sharedProperty.get = userDef
    } else {
        /**
         * ÊØè‰∏ÄÊ¨°ÂèñÂÄºÈÉΩ‰ºöËµ∞getÔºå
         * Â¶ÇÊûúÂèëÁé∞ÊòØËÑèÁöÑÔºåÂ∞±ÈáçÊñ∞Ëé∑Âèñ
         * Â¶ÇÊûú‰∏çÊòØËÑèÁöÑÔºåÂ∞±‰∏çËµ∞get
         * */
        // sharedProperty.get = userDef.get
        sharedProperty.get = createComputedGetter(key)
        sharedProperty.set = userDef.set
    }
    // Êà™Ê≠¢Âà∞ËøôÈáåÔºåwatcher ËøòÂè™ÊòØ‰∏Ä‰∏™Object.definePropertyÔºåÊ≤°Êúâ‰ΩøÁî®Âà∞watcher
    // Êé•‰∏ãÊù•Â∞±Ë¶ÅÂä†ÁºìÂ≠ò
    Object.defineProperty(vm, key, sharedProperty)
}

function createComputedGetter(key) {
    return function computedGetter() {
        // ÂèñËÆ°ÁÆóÂ±ûÊÄßÁöÑÂÄºÔºåËµ∞ÁöÑÊòØËøô‰∏™ÂáΩÊï∞
        // Ë∞ÅÂèñÂÄºÔºåthisÂ∞±ÊòØË∞ÅÔºåÊâÄ‰ª•Ë¶ÅËé∑ÂèñÂà∞watcherÔºåÂ∞±ÂèØ‰ª•ÊääwatcherÊåÇËΩΩÂà∞vm‰∏ä
        // ÈÄöËøákeyÂèØ‰ª•ÊãøÂà∞ÂØπÂ∫îÁöÑwatcher
        // Ëøô‰∏™watcherÂåÖÂê´‰∫Ügetter
        // ‰∏ÄÊó¶Â±ûÊÄßÂèëÁîüÂèòÂåñÔºåÂ∞±ÊâßË°åwatcher‰∏≠ÁöÑgetterÔºàÁî®Êà∑‰º†ËøõÊù•ÁöÑwatchÊñπÊ≥ïÔºâ
        let watcher = this._computedWatchers[key]
        if (watcher.dirty) {
            // Ê†πÊçÆdirtyÂ±ûÊÄßÊù•Âà§Êñ≠ÊòØÂê¶ÈúÄË¶ÅÊ±ÇÂÄº 
            watcher.evaluate()
        }
        /**
         * Â¶ÇÊûúÂΩìÂâçÂèñÂÆåÂÄºÂêéÔºåDep.targetËøòÊúâÂÄºÔºåÈúÄË¶ÅÁªßÁª≠Âêë‰∏äÊî∂ÈõÜ
         */
        if (Dep.target) {
            // ËÆ°ÁÆóÂ±ûÊÄßwatcherÂÜÖÈÉ®Êúâ‰∏§‰∏™dep 
            console.log('dep.target', Dep.target)
            watcher.depend() //watcherÈáåÈù¢ÂØπÂ∫î‰∫ÜÂ§ö‰∏™dep
        }

        return watcher.value
        /**
         * 
         * computed‰∏≠ÁöÑ‰æùËµñÊî∂ÈõÜÔºåÂè™Êî∂ÈõÜcomputer‰∏≠ÁöÑwatcehr
         * ËÆ°ÁÆóÂá∫Êù•ÁöÑfullNameÊòØÊ≤°ÊúâËÆ∞ÂΩïwatcehrÁöÑ
         * firstnameÊòØÂú®computed‰∏≠‰ΩøÁî®ÁöÑÔºåÊâÄ‰ª•ÔºåÂè™‰ºöÊî∂ÈõÜcomputedÁöÑwatcherÔºå‰∏ç‰ºöÊî∂ÈõÜÊ∏≤Êüìwatcher
         * ËÆ°ÁÆóÂ±ûÊÄß‰∏≠ÁöÑÂÄºÔºåÂ∫îËØ•ËÆ∞ÂΩïËÆ°ÁÆóÂ±ûÊÄßwatcherÂíåÊ∏≤Êüìwatcher
         */
    }
}