import { nextTick } from '../util';
// 1. ÂéªÈáç 2. Èò≤Êäñ 
let queue = []
let has = {} // ÂàóË°®Áª¥Êä§Â≠òÊîæ‰∫ÜÂì™‰∫õwatcher
function flushSchedulerQueue() {
    for (let i = 0; i < queue.length; i++) {
        queue[i].run()
    }
    queue = []
    has = {}
    pending = false
}
let pending = false
export function queueWatcher(watcher) {
    // Â§öÊ¨°Êõ¥Êñ∞Ôºå‰ºöÊî∂Âà∞Â§ö‰∏™watcher
    const id = watcher.id
    if (has[id] == null) {
        queue.push(watcher)
        has[id] = true
        // ÂºÄÂêØ‰∏ÄÊ¨°Êõ¥Êñ∞Êìç‰Ωú ÊâπÂ§ÑÁêÜ ÔºàÈò≤ÊäñÔºâ
        if (!pending) {
            nextTick(flushSchedulerQueue, 0)
            pending = true
        }
    }
}